.flip-container {
    perspective: 1000px;
    margin: 1%;
  }
  .flip-container.clicked .flipper {
    transform: rotateY(180deg);
  }
  .flip-container,
  .front,
  .back {
        width: 343px;
        height: 343px;
  }
  .flipper {
    transition: 0.6s;
    transform-style: preserve-3d;
    position: relative;
  }
  .front,
  .back {
    backface-visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
  }
  .front {
    z-index: 2;
    transform: rotateY(0deg);
    background-color: $colorPrincipal;
    cursor: pointer;
    @include displayFlex;
    flex-direction: column;
  }
  .back {
    transform: rotateY(180deg);
    background-color: $colorPrincipal;
    @include displayFlex;
    flex-direction: column;
  }

  .txt-titleCard{
    color: $colorSecondary;
    font-family: $fontPrincipal;
    font-size: 3rem;
    text-align: center;
  }

  .txt-card{
    color: $colorSecondary;
    font-family: $fontSecondary;
    font-size: 1.5rem;
    padding: 3%;
    text-align: center;
  }

.bt-cardStroke{
    @include btStroke;
    border: 3px solid $colorSecondary;
    cursor: pointer;
    @media (min-width: $breakpointSmall) {
      border: 5px solid $colorSecondary;
    }
}

.bt-cardStrokeOops{
    @include btStroke;
    border: 3px dashed $colorSecondary;
    cursor: default;
    font-size: 1rem;
    @media (min-width: $breakpointSmall) {
      border: 2px dashed $colorSecondary;
      font-size: 1.5rem;
    }
}

.bt-cardFill{
    @include btFill;
    border: 3px solid $colorSecondary;
    cursor: pointer;
    @media (min-width: $breakpointSmall) {
      border: 5px solid $colorSecondary;
    }
}

.bt-cardFillOops{
  @include btFill;
  border: 3px solid $colorSecondary;
  cursor: default;
  opacity: 0.6;
  font-size: 1rem;
    @media (min-width: $breakpointSmall) {
      border: 2px dashed $colorPrincipal;
      font-size: 1.5rem;
    }
}

.bt-info{
    position: absolute;
    top: 10px;
    right: 10px;
    width: 25px;
    height: 25px;
    border-radius: 25px;
    border: none;
    background-color: $colorSecondary;
    font-family: $fontSecondary;
    color: $colorPrincipal;
    font-size: 1rem;
    font-weight: bold;
    cursor: pointer;
}

.a-card{
  @include displayFlex;
    width: 200px;
    height: 70px;
    text-decoration: none;
    margin: 3%;
}